export default {
    // 登入Account驗證
    login: function(item) {
        return new Promise((resolve, reject) => {
            let accounts = [
                {
                    id: 'heineken751121@gmail.com',
                    pass: '12345'
                }
            ]
            // 一般會在這邊AJAX 呼叫後端,這邊簡單演示狀況
            // 帳號存在嗎?
            let isAccountExist = accounts.some( (i) => {
                return i.id === item.id
            })

            if (isAccountExist){
                // 帳號密碼正確嗎?
                let isAccountSuccess = accounts.every( (i) => {
                    return i.id === item.id && i.pass === item.pass
                })
                if(isAccountSuccess) {
                    resolve({data: {
                        id: 1,
                        name: '王郁翔'
                    }})
                } else {
                    reject({error: 1})
                }
            } else {
                reject({error: 10001})
            }
        })
    }
}
